import React, { useContext, useState, useEffect } from 'react'
import { MakeContext } from '../MakeContext'
import asyncState from '../../utils/asyncStates'
import invoke from '../../utils/invoke'
import { LocaleContext } from '../../contexts/LocaleContext'
import Locale from '../../components/Locale'
import { ItemContext } from '../../contexts/ItemContext'
import loadImages from '../../utils/loadImages'
import { throttle, InitializationThrottle } from '../../utils/throttle'
import RequestActivityIndicator from '../../components/RequestActivityIndicator'
import imageTools from '../../utils/imageTools'
import AlertModal from '../../components/AlertModal'
import uuid from '../../utils/uuid'
import addCopyrightToCanvas from '../../utils/addCopyrightToCanvas'
import checkCopyright from '../../utils/checkCopyright'
import getEffectType from '../../utils/getEffectType'
import getStamps from '../../utils/getStamps'
import getQualityFromEnv from '../../utils/getQualityFromEnv'

const MODAL_HIDE_TIMING = 800 // millisecond
const NAVIGATE_TIMING = 600

export default function AddCartButton (props) {
  const make = useContext(MakeContext)
  const item = useContext(ItemContext)
  const languageType = useContext(LocaleContext)
  const [modalVisible, setModalVisible] = useState(false)
  const [cartConfirmURL, setCartConfirmURL] = useState(null)
  const [uploadDesignState, setUploadDesignState] = useState(asyncState.none)
  const [uploadDesignError, setUploadDesignError] = useState(null)
  const [type, setType] = useState('GENERATING')
  const [cartItemCode, setCartItemCode] = useState('')
  const [alertModelVisible, setAlertModelVisible] = useState(false)
  const { realWidth, realHeight, canvasArea: { width, height } } = item

  useEffect(() => {
    InitializationThrottle()
  }, [])

  useEffect(() => {
    if (cartConfirmURL) {
      setTimeout(() => {
        setModalVisible(false)
        invoke('navigateTo', {
          routeName: 'URLScreen',
          params: {
            closeBtn: true,
            uri: cartConfirmURL
          }
        })
      }, NAVIGATE_TIMING)
    }
  }, [cartConfirmURL])

  async function handlePressAddCart () {
    try {
      if (uploadDesignState === asyncState.pending) return
      if (uploadDesignState === asyncState.fulfilled && make.getBaseItem().item_code === cartItemCode) {
        invoke('navigateTo', {
          routeName: 'URLScreen',
          params: {
            uri: cartConfirmURL,
            closeBtn: true
          }
        })
        return
      }
      setUploadDesignError(null)
      setModalVisible(true)
      setUploadDesignState(asyncState.pending)
      setType('GENERATING')

      if (!Array.isArray(make.layers) || make.layers.length === 0) {
        // TODO i18n 目前这里到错误信息不会显示在UI上
        throw new Error('程序数据结构错误')
      } else {
        let images = await loadImages(props.data, props.topLayer/*, bottomImgData && bottomImgData.image */)
        let scaledCanvas = imageTools.mergeImage(width, height, images.filter(Boolean), true)
        if (process.env.REACT_APP_SCALE_IMAGE_BEFORE_UPLOAD === 'always') {
          scaledCanvas = imageTools.scaleImage(
            scaledCanvas,
            width,
            height,
            realWidth,
            realHeight
          )
        }

        let copyrightText = checkCopyright(make.layers)
        if (copyrightText) {
          scaledCanvas = await addCopyrightToCanvas(scaledCanvas, copyrightText)
        }

        const imageURL = scaledCanvas.toDataURL('image/png', getQualityFromEnv())
        scaledCanvas = null
        setCartItemCode(make.getBaseItem().item_code)
        const printImageId = uuid.v4()
        await invoke('storeImage', { imageName: printImageId, imageData: imageURL })
        setType('UPLOADING')
        const result = await invoke('uploadDesign', {
          itemCode: make.getBaseItem().item_code || '000000',
          effectType: JSON.stringify(getEffectType(make.layers)),
          onlyUpload: '',
          stamps: getStamps(make.layers),
          originalText: '',
          printImageId
        })
        if (result.error) {
          let error = new Error(result.message)
          error.name = result.message
          throw new Error(error)
        }
        if (!result.success) {
          // TODO i18n 目前这里到错误信息不会显示在UI上
          throw new Error('添加购物车异常，请重试')
        } else {
          setCartConfirmURL(result.confirm_url)
          setUploadDesignError(null)
          setUploadDesignState(asyncState.fulfilled)
          setType('COMPLETE')
        }
      }
    } catch (e) {
      // alert(e.message)
      // setAlertModelVisible(true)
      setUploadDesignError(e)
      setUploadDesignState(asyncState.rejected)
    }
  }

  return (
    <div
      onClick={() => throttle(handlePressAddCart)}
      style={{
        width: window.innerWidth - 30,
        height: '52px',
        // marginLeft: (window.innerWidth - 345) / 2,
        marginLeft: 15,
        marginTop: 15,
        textAlign: 'center',
        color: 'white',
        backgroundColor: '#1b1b1b',
        fontSize: 17
      }}>
      <AlertModal visible={alertModelVisible} title='error.network.title' description='error.network.description' text='OK' onClick={() => { setAlertModelVisible(false) }} />
      <img src={require('../../assets/AddToCart.svg')} alt='' style={{ position: 'relative', top: 4 }} />
      <span className='operationText'
        style={{
          lineHeight: '52px',
          marginLeft: '7px',
          fontFamily: languageType.current === 'ja' ? 'NotoSansCJKjp-Bold' : 'UniqloPro-Bold',
          fontSize: languageType.current === 'ja' ? '15px' : '16px'
        }}>

        <Locale code='make.done.addToCart' />
      </span>
      <RequestActivityIndicator
        onHideStart={() => setModalVisible(false)}
        hideTiming={MODAL_HIDE_TIMING}
        visible={modalVisible}
        autoHideStates={[asyncState.rejected, asyncState.fulfilled]}
        state={uploadDesignState}
        error={uploadDesignError}
        type={type}
      />
    </div>
  )
}
