import invoke from '../../utils/invoke'
import loadImages from '../../utils/loadImages'
import { fabric } from '../../utils/fabric'

function getBase64Image ({ doneImageStyle, doneStyle, doneMultiply }, bottomImg, middleImg, topImg, copyRight, scale, activeItem, newCopyrightData) {
  const canvas = document.createElement('canvas')
  canvas.width = doneImageStyle.width * scale
  canvas.height = doneImageStyle.height * scale
  const ctx = canvas.getContext('2d')
  if(doneMultiply) {
    ctx.globalCompositeOperation = 'multiply';
  }
  ctx.drawImage(
    bottomImg,
    0,
    0,
    canvas.width,
    canvas.height
  )
  if (middleImg) {
    ctx.drawImage(
      middleImg,
      doneStyle.left * scale,
      doneStyle.top * scale,
      doneStyle.width * scale,
      doneStyle.height * scale
    )
  }
  if (topImg) {
    ctx.drawImage(
      topImg,
      doneStyle.left * scale,
      doneStyle.top * scale,
      doneStyle.width * scale,
      doneStyle.height * scale
    )
  }
  // ctx.font = `${22 * doneStyle.width * scale / realWidth}px NotoSansCJKjp`
  // ctx.fillStyle = '#1b1b1b'
  // const txtWidth = doneStyle.width * scale + doneStyle.left * scale - ctx.measureText(copyRight).width - 4.2
  // const txtHeight = doneStyle.height * scale + doneStyle.top * scale - 5.6
  if (newCopyrightData) {
    // ctx.fillText(copyRight, txtWidth, txtHeight) // ctx.drawImage(copyrightData, 0, doneStyle.top + (doneStyle.height * 1) + 16, doneStyle.width * scale - 2, doneStyle.height * scale - 2)
    ctx.drawImage(newCopyrightData, -15, 0, canvas.width - 20, canvas.width - 20)
  }
  // ctx.filter = 'blur(0px)'
  // ctx.strokeStyle = '#ffffff'
  // ctx.strokeText(copyRight, txtWidth, txtHeight)
  // ctx.fillText(copyRight, txtWidth, txtHeight)
  const dataURL = canvas.toDataURL('image/png')
  return dataURL
}

export default async function compositeDesign (item, designData, topImgData, scale = 1, copyRight, activeItem) {
  if (!designData && !topImgData) {
    throw new Error('Cannot load design data.')
  }
  const bottomImgData = await invoke('downloadFile', item.doneImageSrc)
  var canvas2 = new fabric.Canvas('copyrightCan', { enableRetinaScaling: false })
  // 取消canvas组选器
  canvas2.selection = false
  var loremIpsumDolor = new fabric.Text(copyRight, {
    fontFamily: 'NotoSansCJKjp',
    stroke: 'white',
    strokeWidth: 1,
    top: item.doneStyle.top + item.doneStyle.height - 10,
    textAlign: 'right',
    fontSize: 40
  })
  loremIpsumDolor.setCoords()
  canvas2.add(loremIpsumDolor)
  canvas2.getObjects()[0].set({
    scaleX: 0.1,
    scaleY: 0.1,
    textAlign: 'right'
  }).setCoords()
  canvas2.getObjects()[0].setPositionByOrigin(new fabric.Point(item.doneImageStyle.left + item.doneImageStyle.width, item.doneStyle.top + item.doneStyle.height + 10), 'right', 'top')
  canvas2.getObjects()[0].setCoords()
  canvas2.renderAll()
  const copyrightData = canvas2.toDataURL()
  const [bottomImg, middleImg, topImg, newCopyrightData] = await loadImages(bottomImgData.image, designData, topImgData, copyrightData)
  return getBase64Image(item, bottomImg, middleImg, topImg, copyRight, scale, activeItem, newCopyrightData)
}
