
const sourceEnums = {
  paint: 1,
  text: 2,
  photo: 3,
  stamp: 4
}

const effectEnums = {
  '1': 5, // MoMA Eary Modern
  '2': 6, // MoAM Pop Art
  '3': 4, // AUTO DESIGNER
  '4': 1, // SPLASH
  '5': 2, // GLITCH
  '6': 3, // MOSAIC
  '7': 0
}

/**
 *
 * @param {object[]} layers
 * @param {("1"|"2"|"3"|"4"|"5"|"6")} layers[].mixType
 * @param {("paint"|"text"|"picture"|"stamp")} layers[].type
 * @returns {any[]}
 */
export default function getEffectType (layers) {
  let result = []
  layers.forEach(layer => {
    let source = sourceEnums[layer.type]
    let effect = effectEnums[String(layer.mixType)] || 0
    if (effect === 0) {
      result.push([source])
    } else {
      result.push([source, effect])
    }
  })
  return result
}
