import React from 'react'
import UAParser from 'ua-parser-js'

function getOS () {
  const parser = new UAParser()
  const UAOS = parser.getOS()
  return UAOS.name === 'iOS' ? 'ios' : 'android'
}

export default function useTotalMemory () {
  const [OS] = React.useState(getOS())
  return {
    OS
  }
}
