import React from 'react'
import invoke from '../utils/invoke'
import asyncEnums from '../utils/asyncStates'

function formatMemory (raw, level = 'GB') {
  const num = Number(raw)
  const list = [2]
  let index = 0
  while (num >= list[index]) {
    index++
    list.push(Math.pow(2, index))
  }
  const byte = list[list.length - 2] || 0
  return byte / Math.pow(1024, ['B', 'MB', 'GB'].indexOf(level) + 1)
}

export default function useTotalMemory () {
  const [memory, setMemory] = React.useState(null)
  const [state, setState] = React.useState(asyncEnums.none)
  const [error, setError] = React.useState(null)

  const invokeTotalMemory = React.useCallback(async () => {
    try {
      setError(null)
      setState(asyncEnums.pending)
      const result = await invoke('getDeviceInfo', 'getTotalMemory')
      if (process.env.NODE_ENV === 'development') {
        invoke('log', `current device memory is ${formatMemory(result)}GB.`)
      }
      setMemory(formatMemory(result), 'GB')
      setState(asyncEnums.fulfilled)
    } catch (e) {
      setError(e)
      setState(asyncEnums.rejected)
    }
  }, [])

  React.useEffect(() => {
    invokeTotalMemory()
  }, [invokeTotalMemory])

  return {
    pending: [asyncEnums.none, asyncEnums.pending].includes(state),
    error,
    memory
  }
}
