import React, { useContext, useState, useEffect } from 'react'
import { MakeContext } from '../MakeContext'
import ShareButton from './ShareButton'
import Navbar from '../navbar'
import { ItemContext } from '../../contexts/ItemContext'
import invoke from '../../utils/invoke'
import Locale from '../../components/Locale'
import { LocaleContext } from '../../contexts/LocaleContext'
import './done.css'
import IsAgainEdit from '../../components/isAgainEdit'
import UtidButton from './utidButton'
import AddCartButton from './AddCartButton'
import Enlarge from '../../components/Enlarge'
import ViewPort from '../../components/ViewPort'
import SaveButton from './SaveButton'
import { throttle, InitializationThrottle } from '../../utils/throttle'
import AlertModal from './AlertModal'
import injectImages from '../../hoc/injectImages'
import useTotalMemory from '../../hooks/useTotalMemory'
import usePlatform from '../../hooks/usePlatform'

function Done (props) {
  const make = useContext(MakeContext)
  const item = useContext(ItemContext)
  const languageType = useContext(LocaleContext)
  const [isAgainEdit, setIsAgainEdit] = useState(false)
  const [addLimitVisible, setAddLimitVisible] = useState(false)
  const [useLimitVisible, setUseLimitVisible] = useState(false)
  const [moreHeight, setMoreHeight] = useState(0)
  const [alertModelVisible, setAlertModelVisible] = useState(false)
  const [itemType, setItemType] = useState(true)
  const [upStep, setUpStep] = useState(0)
  const totalMemory = useTotalMemory()
  const platform = usePlatform()

  const {
    layers,
    revokeToMake,
    setRevokeToMake,
    previousPage
  } = make
  const { zoom, setZoom } = item
  let copyRightDone = ''
  layers.forEach(element => {
    if (element.copyRight && copyRightDone === '') {
      copyRightDone = element.copyRight
    }
  })

  // 颜色数组
  useEffect(() => {
    InitializationThrottle()
    // invoke
    // invoke('log', layers[layers.length - 1].mixData)
    if ((window.innerWidth - 30) > (window.innerHeight - (244 + 50 * layers.length))) {
      setMoreHeight((window.innerHeight - (244 + 50 * layers.length)))
    } else {
      setMoreHeight((window.innerWidth - 30))
    }
  }, [])
  useEffect(() => {
    if (copyRightDone && item.doneStyle && item.doneImageStyle) {
      try {
        if (itemType) {
          // 存储首次加载衣底时的item.doneImageStyle.width，以便在下面判断是放大缩小操作还是item切换操作
          setUpStep(item.doneImageStyle.width)
          setItemType(false)
          addCopyRight()
        } else {
          if (upStep === item.doneImageStyle.width) {
            // 放大缩小
          } else {
            addCopyRight()
          }
        }
      } catch (e) {

      }
    }
  }, [item])
  function addCopyRight () {
    // doneImageStyle已更新所以刷新copyright位置(item切换)
    setUpStep(item.doneImageStyle.width)
    const newCan = document.getElementById('copyright')
    const ctx = newCan.getContext('2d')
    ctx.font = '2.8px arial'
    ctx.fillStyle = '#1b1b1b'
    ctx.filter = 'blur(0px)'
    ctx.strokeStyle = 'rgba(255,255,255,0.25)'
    ctx.lineWidth = '1'
    let txtWidth = (item.doneStyle.width + item.doneStyle.left) - ctx.measureText(copyRightDone).width
    let txtHeight = (item.doneStyle.top + item.doneStyle.height) - 2.5
    ctx.strokeText(copyRightDone, txtWidth, txtHeight)
    ctx.fillText(copyRightDone, txtWidth, txtHeight)
  }
  async function addlayer () {
    if (layers.length > (getDeviceLayersNumberLimit() - 1)) {
      return setAddLimitVisible(true)
    } else if (checkLimit()) {
      return setUseLimitVisible(true)
    } else {
      try {
        let stampOnly = await invoke('getStampOnly')
        if (stampOnly === true) {
          await invoke('setLayers', [...layers, { type: 'stamp' }])
          invoke('navigateTo', {
            to: '/make/stamp',
            params: { hasLayer: 1, previousPage }
          })
        } else {
          await invoke('setLayers', [...layers, {}])
          invoke('navigateTo', {
            to: '/make',
            params: { itemModal: 'show', hasLayer: 1, previousPage }
          })
        }
      } catch (error) {
        setAlertModelVisible(true)
      }
    }
  }

  function change () {
    if (revokeToMake) {
      invoke('navigateTo', {
        routeName: 'Make'
      })
    } else {
      setIsAgainEdit(true)
      setRevokeToMake(false)
    }
  }

  function isAgainFu (e) {
    setIsAgainEdit(false)
  }

  function getDeviceLayersNumberLimit () {
    if (process.env.REACT_APP_ADD_LAYER_LIMIT_MODE === 'min-memory') {
      let minMemoryLimitNumber = Number(process.env.REACT_APP_ADD_LAYER_LIMIT_NUMBER)
      if (isNaN(minMemoryLimitNumber)) minMemoryLimitNumber = 1

      if (make.layers.length > (minMemoryLimitNumber - 1)) {
        if (platform.OS === 'android') {
          if (totalMemory.memory < 4) return minMemoryLimitNumber
        } else if (platform.OS === 'ios') {
          if (totalMemory.memory <= 2) return minMemoryLimitNumber
        }
      }
    }

    return 3
  }

  function checkLimit () {
    const layersNumberLimit = getDeviceLayersNumberLimit()

    let limitArr = {}
    layers.map(layer => {
      if (layer.type === 'stamp') {
        let stampFlag = layer.stampRule.stampFlag
        limitArr.flag_layer_paint = stampFlag.flag_layer_paint === 'NG' ? 'NG' : ''
        limitArr.flag_layer_text = stampFlag.flag_layer_text === 'NG' ? 'NG' : ''
        limitArr.flag_layer_stamp = stampFlag.flag_layer_stamp === 'NG' ? 'NG' : ''
        limitArr.flag_layer_photo = stampFlag.flag_layer_photo === 'NG' ? 'NG' : ''
      }
    })
    return (limitArr.flag_layer_paint === 'NG' && limitArr.flag_layer_text === 'NG' && limitArr.flag_layer_stamp === 'NG' && limitArr.flag_layer_photo === 'NG') || make.layers.length > (layersNumberLimit - 1)
  }

  function enlarge () {
    setZoom(!zoom)
  }

  function newTop () {
    let marginSize = 0
    if ((window.innerWidth - 30) < (window.innerHeight - (244 + 50 * layers.length))) {
      marginSize = ((window.innerHeight - (244 + 50 * layers.length)) - (window.innerWidth - 30)) / 2
    }
    return marginSize - 10
  }

  return (
    <div
      style={{
        position: 'absolute',
        left: 0,
        right: 0,
        bottom: 0,
        top: 0,
        overflow: 'hidden'
      }}
    >
      <UtidButton data={props.mixData} topLayer={props.topLayer} />
      <AlertModal visible={alertModelVisible} title='error.network.title' description='error.network.description' text='OK' onClick={() => { setAlertModelVisible(false) }} />
      {
        !zoom ? <Navbar page='done' revoke={() => {
          change()
        }} /> : ''
      }
      <div className='doneContainer'
        onClick={() => enlarge()}
        style={{
          height: (window.innerWidth - 30) > (window.innerHeight - (244 + 50 * layers.length)) ? (window.innerHeight - (244 + 50 * layers.length)) : (window.innerWidth - 30),
          width: '100%',
          position: 'absolute',
          zIndex: 1,
          marginTop: zoom ? 'initial' : newTop()
        }}
      >
        <div style={{ display: zoom ? 'block' : 'none', position: 'fixed', top: 0, right: 0, left: 0, bottom: 0, backgroundColor: '#fff' }} />
        <ViewPort
          topLayer={props.topLayer}
          data={props.mixData}
          size={(window.innerWidth - 30) > (window.innerHeight - (244 + 50 * layers.length)) ? (window.innerHeight - (244 + 50 * layers.length)) : (window.innerWidth - 30)}>
          <div style={{ position: 'absolute', top: 0, left: 0 }}>
            <canvas id={'copyright'} width={item.doneImageStyle.width} height={item.doneImageStyle.height} />
            <canvas id={'copyrightCan'} width={item.doneImageStyle.width} height={item.doneImageStyle.height} style={{ display: 'none' }} />
          </div>
          {/* 一层透明遮罩层防止点击canvas触发未知错误 */}
          <div style={{ position: 'absolute', left: 0, top: 0, width: item.doneImageStyle.width, height: item.doneImageStyle.height, background: 'rgba(0,0,0,0)' }} />
        </ViewPort>

      </div>
      <Enlarge showStatus={!zoom ? 'show' : 'hidden'}
        right={15}
        top={newTop() + moreHeight + 15}
        width={40}
        height={40}
      />
      <div id='operationContainer' style={{
        overflow: 'hidden',
        position: 'absolute',
        bottom: 15,
        zIndex: zoom ? 'initial' : 2
      }}
      >
        {(() => {
          if (process.env.REACT_APP_ADD_LAYER_LIMIT_MODE === 'min-memory') {
            if (totalMemory.pending) return null
          }
          return (
            <div style={{
              width: '120px',
              height: '42px',
              backgroundColor: 'white',
              border: '1px solid #dadada',
              color: checkLimit() ? '#ababab' : '#1b1b1b',
              marginLeft: (window.innerWidth - 120) / 2,
              marginTop: 10,
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center'
            }} onClick={() => throttle(addlayer)}>
              <img src={require(checkLimit() ? '../../assets/prohibit.svg' : '../../assets/LayerAdd_Normal.svg')} alt='' />
              <span style={{
                fontWeight: '500',
                marginLeft: '7px',
                fontFamily: languageType.current === 'ja' ? 'NotoSansCJKjp-Bold' : 'UniqloPro-Bold',
                fontSize: languageType.current === 'ja' ? '11px' : '13px'
              }}>
                <Locale code='make.done.addlayer' />
              </span>
            </div>
          )
        })()}
        {
          make.layers.map((item, index) => {
            if (!item.type) { return }
            return <div key={index} style={{
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              marginTop: index === 0 ? 12 : 10,
              width: 345,
              marginLeft: (window.innerWidth - 345) / 2,
              letterSpacing: '0.4px'
            }}>
              <img style={{
                width: 40,
                height: 40
              }} src={require(`../../assets/${item.type}.svg`)} />
              {
                item.mixType === 7 ? ''
                  : <div style={{
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center'
                  }}>
                    <img style={{ margin: '0 5px' }} src={require(`../../assets/and.svg`)} />
                    <img src={require(`../../assets/${item.mixType === 7 ? 'no-mix' : `mix-${item.mixType}`}.svg`)} />
                  </div>
              }
            </div>
          })
        }
        <div style={{
          // width: ' 346px',
          width: window.innerWidth - 30,
          height: '52px',
          marginLeft: 15,
          marginTop: 12
        }}>
          <ShareButton data={props.mixData} topLayer={props.topLayer} />
          <SaveButton data={props.mixData} topLayer={props.topLayer} />
        </div>
        <AddCartButton data={props.mixData} topLayer={props.topLayer} />
      </div>
      <IsAgainEdit status={isAgainEdit} isAgainFu={isAgainFu} />
      <AlertModal
        visible={addLimitVisible}
        title='make.done.addLimit.title'
        description='make.done.addLimit'
        localeParams={[getDeviceLayersNumberLimit()]}
        onClick={() => {
          setAddLimitVisible(false)
        }}
      />
      <AlertModal visible={useLimitVisible} title='make.done.useLimit' onClick={() => {
        setUseLimitVisible(false)
      }} />
    </div>
  )
}

export default injectImages('back')(Done)
