import React, { useEffect } from 'react'
import { Modal } from 'antd-mobile'
import asyncState from '../utils/asyncStates'

export default function RequestActivityIndicator (props) {
  const {
    error,
    visible,
    hideTiming = 800,
    type,
    state,
    onHideStart = () => {},
    autoHideStates = []
  } = props

  useEffect(() => {
    if (autoHideStates.includes(state)) {
      setTimeout(onHideStart, hideTiming)
    }
  }, [state])

  return (
    <Modal
      maskClosable={false}
      visible={visible}
      animated={false}
      style={{
        position: 'absolute',
        top: '50%',
        left: '50%',
        transform: 'translate(-50%,-50%)',
        backgroundColor: '#ffffff',
        zIndex: 8,
        width: '120px',
        height: '120px'
      // padding: '0!important'
      // borderRadius: 10
      }}
    >
      <div
        style={{
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          width: '120px',
          height: '120px',
          margin: 0
        }}
      >
        {(() => {
          if (error) {
            return (
              <div
                style={{
                  display: 'flex',
                  color: '#1b1b1b',
                  fontSize: '15px',
                  fontWeight: 'bold',
                  alignItems: 'center',
                  flexDirection: 'column'
                }}
              >
                <img width={50} height={50} style={{ marginBottom: 40 }} src={require('../assets/failed.svg')} />
                <div style={{
                  position: 'absolute',
                  bottom: 17,
                  height: 22,
                  left:'50%',
                  transform:'translateX(-50%)',
                  fontWeight: 'bold'
                }}>FAILED</div>
              </div>
            )
          }
          if (state !== asyncState.fulfilled) {
            return (
              <div
                style={{
                  display: 'flex',
                  color: '#1b1b1b',
                  fontSize: '15px',
                  fontWeight: 'bold',
                  alignItems: 'center',
                  flexDirection: 'column'
                }}
              ><img width={50} style={{ marginBottom: 30 }} src={require('../assets/LOADING.gif')} />
                <div style={{
                  position: 'absolute',
                  bottom: 20,
                  left:'50%',
                  transform:'translateX(-50%)',
                  fontWeight: 'bold'
                }}>{type || 'LOADING'}
                </div>
              </div>
            )
          }
          if (state === asyncState.fulfilled) {
            return (
              <div
                style={{
                  display: 'flex',
                  color: '#1b1b1b',
                  fontSize: '15px',
                  fontWeight: 'bold',
                  alignItems: 'center',
                  flexDirection: 'column'
                }}
              >
                <img width={50} height={50} style={{ marginBottom: 44 }} src={require('../assets/Complete.svg')} />
                <div style={{
                  position: 'absolute',
                  bottom: 17,
                  left:'50%',
                  transform:'translateX(-50%)',
                  height: 22,
                  fontWeight: 'bold'
                }}>COMPLETE</div>
              </div>
            )
          }
        })()}
      </div>
    </Modal>
  )
}
