import FontManager from './FontManager'

/**
   *
   * @param {HTMLCanvasElement} canvas
   * @param {String} copyrightText
   * @returns {string}
   */
export default async function addCopyrightToCanvas (canvas, copyrightText) {
  const font = await FontManager.load('./fonts/NotoSansCJKjp-Regular.otf')
  const txtWidth = canvas.width - font.getAdvanceWidth(copyrightText, 22) - 9
  const txtHeight = canvas.height - 9.6
  const path = font.getPath(copyrightText, txtWidth, txtHeight, 22)
  const path1 = font.getPath(copyrightText, txtWidth, txtHeight, 22)
  // const path2 = font.getPath(copyrightText, txtWidth, txtHeight, 11)
  // const path3 = font.getPath(copyrightText, txtWidth, txtHeight, 11)
  path.fill = 'transparent'
  path.stroke = 'rgba(255,255,255,0.75)'
  path.strokeWidth = '6'
  path1.fill = '#1b1b1b'
  // path2.fill = '#1b1b1b'
  // path3.fill = '#1b1b1b'
  const ctx = canvas.getContext('2d')
  path.draw(ctx)
  path1.draw(ctx)
  // path2.draw(ctx)
  // path3.draw(ctx)
  return canvas
}
