
export default class FontManager {
  static _cache = {

  }

  /**
   * @param {string} fontPath
   */
  static load = async (fontPath) => {
    if (FontManager._cache[fontPath]) return FontManager._cache[fontPath]
    const font = await new Promise((resolve, reject) => {
      const opentype = require('opentype.js')

      opentype.load(fontPath, function (err, font) {
        if (err) {
          reject(err)
        } else {
          resolve(font)
        }
      })
    })
    FontManager._cache[fontPath] = font
    return font
  }
}
