import React, { useContext, useState, useEffect } from 'react'
import { MakeContext } from '../MakeContext'
import asyncState from '../../utils/asyncStates'
import invoke from '../../utils/invoke'
import { Modal } from 'antd-mobile'
import { LocaleContext } from '../../contexts/LocaleContext'
import Locale from '../../components/Locale'
import { ItemContext } from '../../contexts/ItemContext'
import LoadingModal from './LoadingModal'
import compositeDesign from './compositeDesign'
import code from '../../utils/code'
import useRouter from '../../hooks/useRouter'
import loadImages from '../../utils/loadImages'
import { fabric } from '../../utils/fabric'
import AlertModal from '../../components/AlertModal'
import isSingleWebView from '../../utils/isSingleWebView'
import imageTools from '../../utils/imageTools'
export default function SaveToAlbumButton (props) {
  const make = useContext(MakeContext)
  const item = useContext(ItemContext)
  const languageType = useContext(LocaleContext)
  const [loadingVisible, setLoadingVisible] = useState(false)
  const [panelVisible, setPanelVisible] = useState(false)
  const [isSavedToAPP, setIsSavedToAPP] = useState(!make.isSavePhoto)
  const [isSavedToAlbum, setIsSavedToAlbum] = useState(false)
  const [uploadDesignState, setUploadDesignState] = useState(asyncState.none)
  const [uploadDesignError, setUploadDesignError] = useState(null)
  const [saveDesignModalVisible, setSaveDesignModalVisible] = useState(false)
  const [saveDesignState, setSaveDesignState] = useState(asyncState.none)
  const [saveDesignError, setSaveDesignError] = useState(null)
  const [alertModelVisible, setAlertModelVisible] = useState(false)
  const router = useRouter()
  let activeItem = make.activeItem
  function handleClickSavePhoto () {
    setPanelVisible(true)
  }

  function handleCancel (e) {
    e.stopPropagation()
    setPanelVisible(false)
  }

  function getBase64Image (bottomImg, middleImg, topImg, callback) {
    const { doneImageStyle, doneStyle, realWidth, doneMultiply } = item
    const canvas = document.createElement('canvas')
    // 保存到相册尺寸 1280 * 1280
    const size = 1280
    const scale = size / doneImageStyle.width
    canvas.width = size
    canvas.height = size

    const ctx = canvas.getContext('2d')
    if (doneMultiply) {
      ctx.globalCompositeOperation = 'multiply'
    }
    ctx.drawImage(bottomImg, 0, 0, size, size)
    // ctx.rect(item.doneStyle.left, item.doneStyle.top, item.doneStyle.width, item.doneStyle.height)
    // ctx.stroke()
    if (middleImg) {
      ctx.drawImage(middleImg, doneStyle.left * scale, doneStyle.top * scale, doneStyle.width * scale, doneStyle.height * scale)
    }
    if (topImg) {
      ctx.drawImage(topImg, doneStyle.left * scale, doneStyle.top * scale - 8, doneStyle.width * scale - 2, doneStyle.height * scale - 2)
    }
    let copyRight = ''
    for (let index = 0; index < make.layers.length; index++) {
      let layer = make.layers[index]
      if (layer.hasOwnProperty('copyRight') && layer.copyRight != null && String(layer.copyRight).length > 0) {
        if (String(layer.copyRight).indexOf('(L)') > -1) {
          layer.copyRight = String(layer.copyRight).replace('(L)', 'Ⓛ')
        }
        copyRight = layer.copyRight
        break
      }
    }
    if (copyRight) {
      ctx.font = `${22 * doneStyle.width * scale / realWidth}px arial`
      // if (activeItem[2].name === 'BLACK' || activeItem[2].name === 'GRAY' || activeItem[2].name === 'NAVY') {
      //   ctx.fillStyle = '#fff'
      // } else {
      // ctx.fillStyle = '#1b1b1b'
      // }
      const txtWidth = doneStyle.width * scale + doneStyle.left * scale - ctx.measureText(copyRight).width - 4.2
      const txtHeight = doneStyle.height * scale + doneStyle.top * scale
      // invoke('log', 'txtWidth-->' + txtWidth + ';txtHeight-->' + txtHeight)
      // ctx.fillText(copyRight, txtWidth, txtHeight)

      // ctx.font = '22px "NotoSansCJKjp"'
      ctx.fillStyle = '#1b1b1b'
      // let txtWidth = realWidth - ctx.measureText(copyRight).width
      // let txtHeight = realHeight - 5.6
      ctx.filter = 'blur(0px)'
      ctx.strokeStyle = '#ffffff'
      ctx.strokeText(copyRight, txtWidth, txtHeight)
      ctx.fillText(copyRight, txtWidth, txtHeight)
    }

    const dataURL = canvas.toDataURL('image/png')

    callback(dataURL)
  }

  async function handlePressSaveData () {
    try {
      if (isSavedToAPP) return
      if (saveDesignState === asyncState.pending) return

      setPanelVisible(false)
      setSaveDesignModalVisible(true)
      setSaveDesignState(asyncState.pending)

      let layers = make.layers
      /**
       * 320是缩略图对宽度
       * 这里只保存缩略图
       */
      const middleImgData = props.data
      const topImgData = props.topLayer
      let copyRight = ''
      for (let index = 0; index < make.layers.length; index++) {
        let layer = make.layers[index]
        if (layer.hasOwnProperty('copyRight') && layer.copyRight != null && String(layer.copyRight).length > 0) {
          if (String(layer.copyRight).indexOf('(L)') > -1) {
            layer.copyRight = String(layer.copyRight).replace('(L)', 'Ⓛ')
          }
          copyRight = layer.copyRight
          break
        }
      }
      const thumb = await compositeDesign(item, middleImgData, topImgData, 320 / item.doneImageStyle.width, copyRight, activeItem)
      const passProps = code.decodeURI(router.query.passProps || '{}')
      if (isSingleWebView() && imageTools.cache) {
        await Promise.all([
          invoke('storeImage', { imageName: `back-${layers.length}`, imageData: middleImgData }),
          topImgData && invoke('storeImage', { imageName: 'topLayer', imageData: topImgData })
        ])
      }
      await invoke('saveDesign', { layers, thumb, designId: passProps.designId })
      setIsSavedToAPP(true)
      setSaveDesignState(asyncState.fulfilled)
      make.setRevokeToMake(true)
    } catch (e) {
      setAlertModelVisible(true)
      setSaveDesignState(asyncState.rejected)
      setSaveDesignError(e)
    }
    setTimeout(() => {
      setSaveDesignModalVisible(false)
      setSaveDesignState(asyncState.none)
      setSaveDesignError(null)
    }, 800)
  }

  async function handlePressSavePhoto () {
    try {
      if (isSavedToAlbum) return
      if (uploadDesignState === asyncState.pending) return

      if (uploadDesignState === asyncState.fulfilled) {
        return
      }

      setPanelVisible(false)
      setLoadingVisible(true)
      setUploadDesignState(asyncState.pending)

      // invoke('log', '------item------')
      // invoke('log', item)
      const bottomImgData = await invoke('downloadFile', item.doneImageSrc)
      loadImages(bottomImgData.image, props.data, props.topLayer)
        .then(([bottomImg, middleImg, topImg, copyrightData]) => {
          getBase64Image(bottomImg, middleImg, topImg, async (imageData) => {
            const result = await invoke('saveToAppOrLocalAlbum', { imageData })
            if (result.success) {
              setIsSavedToAlbum(true)
              setUploadDesignState(asyncState.fulfilled)
            }
            // else {
            // setUploadDesignState(asyncState.rejected)
            // setUploadDesignError(true)
            // }
            setTimeout(() => {
              setLoadingVisible(false)
              setUploadDesignState(asyncState.none)
              setUploadDesignError(null)
            }, 800)
          })
        })
    } catch (e) {
      setUploadDesignState(asyncState.rejected)
      setUploadDesignError(e)
      setTimeout(() => {
        setLoadingVisible(false)
        setUploadDesignState(asyncState.none)
        setUploadDesignError(null)
      }, 800)
    }
  }

  return (
    <div>
      <AlertModal visible={alertModelVisible} title='error.network.title' description='error.network.description' text='OK' onClick={() => { setAlertModelVisible(false) }} />
      <div onClick={handleClickSavePhoto} className='operation' style={{ float: 'right', width: (window.innerWidth - 45) / 2 }}>
        <img src={require('../../assets/newSave.svg')} alt='' style={{ position: 'relative', top: 8 }} />
        <span style={{
          position: 'relative',
          lineHeight: '52px',
          top: '-2px',
          marginLeft: '7px',
          fontFamily: languageType.current === 'ja' ? 'NotoSansCJKjp-Bold' : 'UniqloPro-Bold',
          fontSize: languageType.current === 'ja' ? '15px' : '16px'
        }}>
          <Locale code='make.done.save' />
        </span>
      </div>
      <LoadingModal
        type='SAVING'
        setVisible={() => setLoadingVisible(false)}
        visible={loadingVisible}
        state={uploadDesignState}
        error={uploadDesignError}
      />
      <LoadingModal
        type='SAVING'
        setVisible={() => setSaveDesignModalVisible(false)}
        visible={saveDesignModalVisible}
        state={saveDesignState}
        error={saveDesignError}
      />
      <Modal
        popup
        onClose={() => setPanelVisible(false)}
        visible={panelVisible}
        animationType='slide-up'
        // style={{
        //   position: 'absolute',
        //   backgroundColor: '#fff',
        //   left: 0,
        //   bottom: 0,
        //   margin: 0,
        //   width: window.innerWidth,
        //   height: '194px'
        // }}
      >
        <div style={{
          display: 'flex',
          flexDirection: 'column',
          height: '194px',
          overflow: 'hidden'
        }}>
          <div onClick={handlePressSaveData} style={{
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            padding: '0 16px',
            height: '56px'
          }}>
            <div>
              <img style={{
                width: 24,
                height: 24
              }} src={require(`../../assets/Save${isSavedToAPP ? '_Press' : ''}.svg`)} />
            </div>
            <div style={{
              flex: 1,
              textAlign: 'center',
              fontWeight: 'bold',
              height: '56px',
              display: 'flex',
              flexDirection: 'column'
            }}>
              <span style={{
                fontFamily: languageType.current === 'ja' ? 'NotoSansCJKjp-Bold' : 'initial',
                fontWeight: languageType.current === 'ja' ? 'initial' : 'bold',
                fontSize: languageType.current === 'ja' ? '17px' : '20px',
                color: isSavedToAPP ? '#ababab' : '#1b1b1b',
                marginTop: '5px'
              }}>
                <Locale code='make.done.saveToApp' />
              </span>
              <span style={{
                fontFamily: languageType.current === 'ja' ? 'NotoSansCJKjp-Bold' : 'initial',
                fontWeight: languageType.current === 'ja' ? 'initial' : 'bold',
                fontSize: languageType.current === 'ja' ? '11px' : '10px',
                color: isSavedToAPP ? '#ababab' : '#7d7d7d'
              }}>
                <Locale code='make.done.saveToApp.desc' />
              </span>
            </div>
          </div>
          <div onClick={handlePressSavePhoto} style={{
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            padding: '0 16px',
            height: '56px',
            borderTop: 'solid 1px #dadada',
            borderBottom: 'solid 1px #dadada'
          }}>
            <div>
              <img style={{
                width: 24,
                height: 24
              }} src={require(`../../assets/Album${isSavedToAlbum ? '_press' : ''}.svg`)} />
            </div>
            <div style={{
              flex: 1,
              textAlign: 'center',
              fontWeight: 'bold',
              height: '56px',
              display: 'flex',
              flexDirection: 'column'
            }}>
              <span style={{
                fontFamily: languageType.current === 'ja' ? 'NotoSansCJKjp-Bold' : 'initial',
                fontWeight: languageType.current === 'ja' ? 'initial' : 'bold',
                fontSize: languageType.current === 'ja' ? '17px' : '20px',
                color: isSavedToAlbum ? '#ababab' : '#1b1b1b',
                marginTop: '5px'
              }}>
                <Locale code='make.done.saveToAlbum' />
              </span>
              <span style={{
                fontFamily: languageType.current === 'ja' ? 'NotoSansCJKjp-Bold' : 'initial',
                fontWeight: languageType.current === 'ja' ? 'initial' : 'bold',
                fontSize: languageType.current === 'ja' ? '11px' : '10px',
                color: isSavedToAlbum ? '#ababab' : '#7d7d7d'
              }}>
                <Locale code='make.done.saveToAlbum.desc' />
              </span>
            </div>
          </div>
          <div onClick={handleCancel} style={{ height: '52px', padding: '15px' }}>
            <button type='button' style={{
              borderWidth: 1,
              borderStyle: 'solid',
              borderColor: '#1b1b1b',
              height: '52px',
              // fontWeight: 'bold',
              textAlign: 'center',
              fontSize: '17px',
              width: '100%',
              color: '#000',
              backgroundColor: '#fff'
            }}>
              <span style={{
                fontFamily: languageType.current === 'ja' ? 'NotoSansCJKjp-Bold' : 'UniqloPro-Bold',
                fontSize: languageType.current === 'ja' ? '15px' : '16px'
              }}>
                <Locale code='make.done.save.cancel' />
              </span>
            </button>
          </div>
        </div>
      </Modal>
    </div>
  )
}
